@import "scss/_variables";
@import "scss/_mixins";

.page-title {
  @include breakpoint(size-lg-up) {
    margin: 30px 0;
  }

  :global {
    .page-title--welcome {
      h1.ant-typography {
        font-size: 32px;
        font-weight: normal;
        color: $gray-normal-color;

        span.ant-typography,
        strong {
          color: $primary-color;
          font-weight: 500;
        }
      }

      div.ant-typography {
        font-size: 14px;
        line-height: 1.71;
        letter-spacing: 0.58px;
      }
    }

    .page-title--normal {
      h1.ant-typography {
        font-size: 32px;
        @include title-border;
      }

      div.ant-typography {
        font-size: 14px;
        line-height: 1.71;
        letter-spacing: 0.58px;
      }
    }
  }
}
