@import "_variables.scss";
@import "_mixins.scss";
@import "_functions.scss";
@import "_base.scss";
@import "_layout.scss";

.ant-btn.ant-btn-primary {
  letter-spacing: 2px;
}

.ant-btn.ant-btn-default {
  border: 0;
}

.ant-btn.ant-btn-link {
  color: $gray-normal-color;
}

.ant-space.ant-space-vertical {
  width: 100%;
}

span.anticon {
  font-size: 18px;
  line-height: 0 !important;
  vertical-align: middle;
}

.ant-card.app-more-info-card {
  @include card-more-info;
}

h1 {
  @include title-24-primary;
}

h2 {
  @include title-16-primary;
}

h3 {
  @include title-12-primary;
}

h4 {
  @include title-11-primary;
}

h5,
h6 {
  @include title-10-primary;
}

p {
  @include paragraph-12;
}

// TYPOGRAPHY
//////////////////////////////////////////////////////////////////////////

.ant-typography {
  color: inherit;
}

// TITLES
h1.ant-typography,
.ant-typography h1,
.ant-typography + h1.ant-typography {
  @include title-24-primary;
}

h2.ant-typography,
.ant-typography h2,
.ant-typography + h2.ant-typography {
  @include title-16-primary;
}

h3.ant-typography,
.ant-typography h3,
.ant-typography + h3.ant-typography {
  @include title-12-primary;
}

h4.ant-typography,
.ant-typography h4,
.ant-typography + h4.ant-typography {
  @include title-10-primary;
}

// PARAGRAPHS
div.ant-typography {
  @include paragraph-12;
}

.title-page {
  h1.ant-typography,
  h2.ant-typography,
  h3.ant-typography,
  h4.ant-typography {
    @include title-border;
  }

  div.ant-typography {
    @include paragraph-14;
  }

  .title-center {
    @include title-center;
  }

  .ant-page-header {
    background-color: transparent;
    padding: 0;

    .ant-page-header-heading {
      align-items: center;
    }

    .ant-page-header-heading-left {
      @include breakpoint(size-lg-down) {
        display: block;
      }
    }

    .ant-page-header-heading-title {
      @include title-24-primary;
      @include title-border;
      overflow: visible;
      margin-top: 12px;

      @include breakpoint(size-lg-down) {
        display: block;
        margin-bottom: 0;
      }
    }

    .ant-page-header-heading-sub-title {
      @include breakpoint(size-lg-down) {
        display: block;
        margin-bottom: 16px;
      }

      div {
        @include title-16;
        font-size: 18px;
        font-weight: normal;

        span {
          color: $primary-color;
          font-weight: 500;
        }
      }
    }

    .ant-page-header-heading-extra {
      .ant-btn {
        font-size: 14px;
      }
    }
  }
}

.title-gray {
  color: $gray-normal-color !important;
}

.title-regular {
  font-weight: normal !important;
}

.grid-title-value {
  @include title-value;
  position: relative;
}

.grid-title-value-reverse {
  @include title-value-reverse;
  position: relative;
}

// BUTTONS
//////////////////////////////////////////////////////////////////////////
.ant-btn {
  &.ant-btn-primary {
    text-transform: uppercase;
  }

  &.ant-btn-text {
    color: $coral-color;

    &.ant-btn-sm {
      border-radius: 5px;
      height: auto;
      padding: 5px 7px 3px;
    }
  }
}

// MESSAGES
//////////////////////////////////////////////////////////////////////////
.ant-message {
  .ant-message-notice-content {
    padding: 0;
    border: 0;
    box-shadow: 0 0 0;
    border-radius: 10px;

    .ant-message-custom-content {
      padding: 14px 16px;
      border-radius: 10px;
      box-shadow: 0 12px 20px 0 rgba(0, 1, 79, 0.3);
      display: flex;
      align-items: center;

      &.ant-message-success {
        border: 1px solid $success-color;
        background-color: #caf3e1;

        .anticon {
          display: none;
        }

        .message-content {
          display: flex;
          align-items: center;
          justify-content: center;
        }

        .message-icon,
        .message-close {
          background-repeat: no-repeat;
          background-position: center center;
          background-size: cover;
          flex: 0 0 24px;
          width: 24px;
          height: 24px;
          display: inline-block;
          margin-right: 10px;
        }

        .message-icon {
          background-image: url(../assets/icons/step-done.svg);
        }

        .message-close {
          background-image: url(../assets/icons/close-primary.svg);
          margin-left: 10px;

          @include breakpoint(size-lg-up) {
            margin-left: 120px;
          }
        }
      }

      .message-message {
        @include title-12-primary;
        display: inline-block;
        text-align: left;
        max-width: 370px;
      }
    }
  }
}

// COLUMN
//////////////////////////////////////////////////////////////////////////
.column-separator {
  margin: 0;
  border-color: #979797;
  opacity: 0.5;

  &.ant-divider-vertical {
    height: calc(100% - 24px);
    position: absolute;
    right: 8px;
    top: 50%;
    transform: translate(0, -50%);
  }

  &.ant-divider-horizontal {
    width: 100%;
  }
}

// FORM ELEMENTS
//////////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////////

// FORM
//////////////////////////////////////////////////////////////////////////
.form-separator {
  text-align: center;

  .ant-divider-vertical {
    height: calc(100% - 30px);
    opacity: 0.5;
    border-color: #979797;
  }
}

.ant-form.ant-form-vertical {
  .ant-form-item {
    margin-bottom: 30px;
  }

  .ant-form-item-explain,
  .ant-form-item-extra {
    @include paragraph-10;
    padding: 5px;
    color: $primary-color;
  }

  .ant-form-item-explain {
    position: absolute;
    bottom: -26px;
    left: 0;
  }

  .ant-form-item-label {
    label {
      @include title-11-primary;
      line-height: 1.27;
      padding-left: 2px;
      font-weight: normal;

      &:before {
        display: none;
      }
    }
  }
}

// FORM VALIDATION
//////////////////////////////////////////////////////////////////////////
.ant-form-item {
  &.ant-form-item-has-success {
    .ant-input {
      border-color: $success-color;
      background-image: url(../assets/icons/input-success.svg);
      background-repeat: no-repeat;
      background-size: 16px auto;
      background-position: calc(100% - 10px) 50%;
    }
  }

  &.ant-form-item-has-error {
    .ant-input,
    .ant-form-item-label label,
    .ant-form-item-explain,
    .ant-form-item-extra {
      color: $error-color;
    }

    .ant-radio-group .ant-radio-wrapper {
      border-color: $error-color;
    }

    .ant-form-item-explain,
    .ant-form-item-extra {
      padding-left: 23px;
      background-image: url(../assets/icons/input-error.svg);
      background-repeat: no-repeat;
      background-size: 12px auto;
      background-position: 5px 50%;
    }
  }
}

// FORM INPUT
//////////////////////////////////////////////////////////////////////////
.ant-input {
  padding-bottom: 6px;
}

// FORM SELECT
//////////////////////////////////////////////////////////////////////////
.ant-select.ant-select-show-arrow {
  .ant-select-arrow {
    background-image: url(../assets/icons/select-arrow-down.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    width: 28px;
    height: 28px;
    top: 50%;
    right: 8px;
    margin: 0;
    transform: translate(0, -50%);

    svg {
      display: none;
    }
  }
}

// FORM CHECKBOX
//////////////////////////////////////////////////////////////////////////
.app-checkbox {
  padding: 10px 16px;
  border: 1px solid $gray-light-color;
  border-radius: 4px;
  display: inline-block;

  &.app-checkbox-checked {
    background-color: rgba($primary-color, 0.1);
    .ant-checkbox-wrapper {
      font-weight: bold;
    }
  }

  .ant-checkbox-wrapper {
    @include paragraph-10;
    font-size: 12px;
    color: #444b54;
    font-weight: 500;
  }
}

// FORM RADIO
//////////////////////////////////////////////////////////////////////////
.app-radio-group.ant-radio-group {
  width: 100%;

  .ant-radio-wrapper {
    @include paragraph-10;
    font-size: 12px;
    color: #444b54;
    font-weight: 500;
    padding: 10px 16px;
    border-radius: 4px;
    border: 1px solid $gray-light-color;
    width: 100%;

    &.ant-radio-wrapper-checked {
      background-color: rgba($primary-color, $alpha: 0.1);
      color: $primary-color;
      border-color: $primary-color;
      font-weight: bold;
    }

    .ant-radio-inner {
      border-color: $gray-light-color;
    }
  }

  &.ant-radio-group-small {
    .ant-radio-wrapper {
      padding: 10px 8px;
    }
  }
}

// FISCAL MODAL TRIGGER
//////////////////////////////////////////////////////////////////////////
.ant-form {
  .fiscal-modal-trigger {
    .ant-btn {
      @include paragraph-10;
      position: absolute;
      top: -22px;
      right: 2px;
      font-weight: bold;
      color: #1e212f;
      line-height: normal;

      span {
        text-decoration: underline;
      }
    }
  }
}

// MODAL
//////////////////////////////////////////////////////////////////////////
.ant-modal-mask {
  background-color: rgba($black-color, 0.7);
}

.ant-modal {
  .ant-modal-content {
    border-radius: 10px;
  }

  &.modal-problem {
    div.ant-typography {
      @include paragraph-14;
    }
  }

  .ant-modal-content {
    .ant-modal-close-x span.anticon {
      font-size: 24px;
    }
  }
}

// TABS
//////////////////////////////////////////////////////////////////////////
.ant-tabs {
  &.ant-tabs-top {
    .ant-tabs-nav-more {
      display: none;
    }

    &.ant-tabs-large {
      .ant-tabs-nav-wrap {
        text-align: center;
        border-color: #e6e6e6;

        .ant-tabs-nav-list {
          @include breakpoint(size-lg-up) {
            display: flex;
            justify-content: space-around;
            width: 100%;
          }

          .ant-tabs-ink-bar {
            display: none !important;
          }

          .ant-tabs-tab {
            padding: 16px 7px 3px;
            color: $primary-color;
            border-bottom: 3px solid transparent;

            &.ant-tabs-tab-active {
              border-color: $coral-color;
            }
          }
        }
      }
    }
  }
}

// BILLS SECTION
//////////////////////////////////////////////////////////////////////////
.app-bills-section {
  @include breakpoint(size-lg-up) {
    padding-bottom: 24px;
  }

  .bills-section-titile {
    font-size: 18px;
    font-weight: normal;
    color: $gray-normal-color;
    margin-bottom: 16px;
  }

  .bill-container {
    margin-bottom: 20px;
    overflow: visible;
    @include box-shadow;
    &:after {
      height: 70px;
    }

    &:before {
      content: "";
      display: block;
      position: absolute;
      width: 8px;
      height: 100%;
      top: 0;
      left: 0;
      background-color: transparent;
      border-radius: 10px 0 0 10px;
    }

    &.bill-container--light {
      &:before {
        background-color: $brand-light-color;
      }

      .bill-title {
        background-image: url(../assets/icons/light-icon.svg);
        padding-left: 60px;
      }
    }

    &.bill-container--gas {
      &:before {
        background-color: $brand-gas-color;
      }

      .bill-title {
        background-image: url(../assets/icons/gas-icon.svg);
        padding-left: 60px;
      }
    }

    .bill-title {
      background-repeat: no-repeat;
      background-position: left top;
      background-size: 43px 43px;
      h2.ant-typography {
        span.ant-typography {
          color: $third-color;
          margin-left: 5px;
        }
      }

      div.ant-typography {
        @include paragraph-14;
        color: $gray-normal-color;
      }
    }

    .ant-collapse {
      background-color: transparent;
      border-radius: 0;

      .ant-collapse-item {
        border: 0;

        .ant-collapse-header {
          @include title-14-primary;
          display: inline-block;
          position: absolute;
          top: auto;
          bottom: -16px;
          left: 50%;
          transform: translate(-50%, 0);
          border-radius: 15px;
          background-color: #f4f7fc;
          padding: 7px 8px 6px 32px;
        }

        .ant-collapse-content {
          border-radius: 0;
        }

        .ant-collapse-content-box {
          padding: 0;
        }

        .bill-collapse-btn {
          margin: 16px 0;

          @include breakpoint(size-lg-up) {
            margin: 0;
            text-align: right;
          }
        }
      }
    }
  }
}

// SUPPLIES SECTION
//////////////////////////////////////////////////////////////////////////
.app-supplies-section {
  .supply-container {
    @include box-shadow;
    margin-bottom: 10px;

    .ant-tag {
      margin: 0;
      padding: 2px 10px;
      border-radius: 50px;
      background-color: #e0f8ee;
      color: $primary-color;
      border: 0;
      position: absolute;
      top: 0;
      right: 0;

      span {
        margin: 0;

        &.anticon {
          margin-right: 3px;
        }
      }

      &.tag-disactive {
        background-color: rgba($error-color, 0.4);
      }
    }

    &.supply-container--light {
      .spply-title {
        background-image: url(../assets/icons/light-icon.svg);
        padding-left: 60px;
      }
    }

    &.supply-container--gas {
      .spply-title {
        background-image: url(../assets/icons/gas-icon.svg);
        padding-left: 60px;
      }
    }

    .spply-title {
      background-repeat: no-repeat;
      background-position: left top;
      background-size: 43px 43px;
      padding-bottom: 5px;
      h2.ant-typography {
        margin: 0;
        span.ant-typography {
          color: $third-color;
        }
      }
    }
  }
}

// BILLS & SUPPLIES SECTION
//////////////////////////////////////////////////////////////////////////
.app-bills-section,
.app-supplies-section {
  .ant-divider {
    border-color: #e8e8e8;
  }

  .bill-cell-value,
  .suply-cell-value {
    @include title-value;

    .large-cell {
      h3.ant-typography {
        font-size: 16px;

        span.anticon {
          font-size: 24px;
          margin: 0 5px;
          margin-top: -5px;
        }
      }

      h4.ant-typography {
        font-size: 32px;

        span {
          font-size: 16px;
        }
      }

      .ant-btn {
        &.ant-btn-primary {
          @include breakpoint(size-lg-down) {
            margin-top: 20px;
          }
        }
      }
    }

    .bill-status {
      .ant-tag {
        width: 80px;
        height: 35px;
        border-radius: 17.5px;
        margin: 0;
        color: $primary-color;
        position: relative;

        span {
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          white-space: normal;
          width: 60px;
          line-height: 1;
          text-align: center;
        }
      }

      &.bill-status--pay {
        .ant-tag {
          border-color: rgba(100, 130, 248, 0.2);
          background-color: rgba(100, 130, 248, 0.2);
        }
      }

      &.bill-status--paid {
        .ant-tag {
          border-color: $success-color;
          background-color: rgba($success-color, 0.4);
        }
      }

      &.bill-status--expiring {
        .ant-tag {
          border-color: #ffe300;
          background-color: rgba(#ffe300, 0.4);
        }
      }

      &.bill-status--expired {
        .ant-tag {
          border-color: $error-color;
          background-color: rgba($error-color, 0.4);
        }
      }
    }
  }
}

// CHARTS SECTION
//////////////////////////////////////////////////////////////////////////
.app-charts-section {
  padding-bottom: 30px;

  .ant-card {
    @include box-shadow;
  }
}

// POPOVER
//////////////////////////////////////////////////////////////////////////
.ant-popover {
  &.ant-popover-placement-bottomLeft {
    .ant-popover-content {
      margin-left: -13px;
      margin-top: -3px;
    }

    .ant-popover-arrow {
      border: 0 !important;
      box-shadow: 0 0 0 !important;
      background-image: url(../assets/icons/triangle-primary.svg);
      background-repeat: no-repeat;
      background-position: left top;
      background-size: 100% auto;
      transform: rotate(0);
      width: 30px;
      height: 14px;
      top: -6px !important;
      left: -2px !important;
    }
    .ant-popover-inner {
      width: 200px;
      border: 1px solid $primary-color;

      .ant-popover-inner-content {
        padding: 8px;
      }

      div.ant-typography {
        @include paragraph-10;
      }
    }
  }
}

// APP CAROUSEL
//////////////////////////////////////////////////////////////////////////
.app-carousel {
  margin: 0 -16px;

  &.app-carousel--house-card {
    .slick-list {
      &:after,
      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        width: 30px;
        height: 100%;
        z-index: 10;
      }

      &:before {
        left: 0;
        background: linear-gradient(to right, $white-color, rgba($white-color, 0));
      }

      &:after {
        right: 0;
        background: linear-gradient(to left, $white-color, rgba($white-color, 0));
      }
    }
  }

  .slick-slide {
    padding: 0 16px 24px;
  }

  .slick-dots {
    align-items: center;

    &.slick-dots-top {
      top: 0;
      bottom: auto;
    }

    &.slick-dots-bottom {
      top: auto;
      bottom: -24px;
    }

    li {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $primary-color;
      opacity: 0.5;
      margin: 0 5px;

      &.slick-active {
        width: 8px;
        height: 8px;
        opacity: 1;
        transform: scale(1.5);
      }

      button {
        display: none;
      }
    }
  }
}

// APP TABLE
//////////////////////////////////////////////////////////////////////////
.app-table {
  @include breakpoint(size-md-down) {
    overflow-x: scroll;

    .ant-table-container {
      width: 1000px;
      overflow-x: scroll;
    }
  }

  .ant-table-empty {
    .ant-table-tbody {
      display: none;
    }
  }

  .ant-table {
    .ant-table-thead,
    .ant-table-tbody {
      tr {
        // display: flex;
        // justify-content: space-between;
        // align-items: center;
        padding: 0 24px;
        display: block;
        border-radius: 10px;
        margin-bottom: 16px;

        .ant-table-cell {
          width: 200px;
          border: 0;
          padding: 16px 0;
        }
      }
    }

    .ant-table-thead {
      tr {
        background-color: #fbfbfb;
      }

      .ant-table-cell {
        @include title-14-primary;
        background-color: #fbfbfb;

        &:first-child {
          &:after {
            height: 1000px;
          }
        }
      }
    }

    .ant-table-tbody {
      max-height: 300px;
      overflow-y: scroll;
      display: block;

      &:after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 30px;
        background: linear-gradient(to top, #ffffff, transparent);
      }

      .ant-table-measure-row {
        display: none;
      }

      tr {
        border: 1px solid rgba($primary-color, 0.2);

        &:last-child {
          margin-bottom: 24px;
        }
      }
      .ant-table-cell {
        background-color: $white-color !important;
        padding: 5px;
        @include paragraph-14;
        color: #333333;

        &:first-child {
          &:after {
            display: none;
          }
        }
      }
    }

    &.ant-table-small {
      .ant-table-tbody {
        tr {
          margin-bottom: 8px;

          &:last-child {
            margin-bottom: 16px;
          }

          .ant-table-cell {
            padding: 10px 0;
          }

          &:nth-child(even) {
            background-color: $table-gray-bg;
            border-color: $table-gray-bg;
            .ant-table-cell {
              background-color: $table-gray-bg !important;
            }
          }
        }
      }
    }
  }
}

// FLUID HEADER & FOOTER
//////////////////////////////////////////////////////////////////////////
.app-header-fluid,
.app-footer-fluid {
  position: fixed;
  left: 0;
  width: 100%;
  background-color: #f4f7fc;
  height: 70px;
  padding: 10px 40px;
  z-index: 100;
}

.app-header-fluid {
  top: 0;
  border-bottom: solid 1px #dddddd;

  @include breakpoint(size-lg-down) {
    text-align: center;
  }
}

.app-footer-fluid {
  bottom: 0;
  border-top: solid 1px #dddddd;
  text-align: center;

  div.ant-typography,
  p {
    font-size: 10px;
    line-height: 1.6;
    letter-spacing: 0.5px;
  }
}
