@import "scss/_variables";
@import "scss/_mixins";

.house-tabs {
  margin-top: 30px;

  :global {
    .ant-tabs {
      @include breakpoint(size-lg-down) {
        overflow: visible;
      }

      .ant-tabs-nav {
        border: solid 1px rgba($primary-color, 0.2);
        background-color: $white-color;
        width: calc(100% + 40px);
        margin: 20px -20px 16px;

        @include breakpoint(size-lg-up) {
          width: 100%;
          margin-bottom: 40px;
          margin: 0 0 16px;
          border-radius: 10px;
        }
      }

      .ant-tabs-nav-wrap {
        .ant-tabs-nav-list {
          padding: 10px 16px;

          @include breakpoint(size-lg-up) {
            width: 100%;
            display: flex;
            //justify-content: space-between;
            //align-items: center;
          }

          .ant-tabs-tab {
            @include title-16;
            font-weight: normal;
            color: $third-color;
            padding: 10px 30px;
            margin: 0;

            &.ant-tabs-tab-active {
              background-color: $third-color;
              border-radius: 10px;
              font-weight: 500;

              .ant-tabs-tab-btn {
                color: $white-color;
              }
            }
          }
        }
      }

      .ant-tabs-ink-bar {
        display: none;
      }
    }
  }
}
