@import "scss/_variables";
@import "scss/_mixins";

.banner-full {
  @include box-shadow;

  :global {
    .ant-card-body {
      padding: 0;
      background-color: $primary-color;
      border-radius: 10px;

      .ant-col {
        padding: 24px;
      }

      .image-col {
        padding: 0;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        border-radius: 10px 10px 0 0;
        height: 170px;

        @include breakpoint(size-lg-up) {
          border-radius: 10px 0 0 10px;
        }
      }

      h1.ant-typography,
      h2.ant-typography,
      h3.ant-typography {
        color: $white-color;
      }

      h1.ant-typography {
        font-size: 32px;
        margin: 8px 0 15px;
      }

      h3.ant-typography {
        text-transform: uppercase;
      }
    }
  }
}

.banner-half {
  @include box-shadow;
  margin-bottom: 40px;

  :global {
    .ant-card-cover {
      position: relative;

      &:after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        bottom: 0;
        left: 0;
        background: linear-gradient(to top, rgba($white-color, 1), rgba($white-color, 0));
        z-index: 10;
      }

      .cover-image{
        background-position: center;
        background-size: cover;
        position: relative;
        height: 190px;
      }

      .absolute-section {
        text-align: center;
        padding-bottom: 24px;
        position: absolute;
        width: 100%;
        height: auto;
        left: 0;
        bottom: 0;
        z-index: 20;

        h1.ant-typography {
          font-size: 32px;
        }

        h3.ant-typography {
          @include title-14-primary;
          text-transform: uppercase;
        }
      }
    }

    .ant-btn {
      box-shadow: 0 18px 30px -15px rgba(0, 1, 79, 0.35);
    }
  }
}
