// SPACING
//////////////////////////////////////////////////////////////////////////
$sizes: 0, 5, 10, 15, 16, 20, 24, 25, 30, 40, 50, 60, 70, 80, 90, 100;

@each $size in $sizes {
  // MARGINS
  .m-#{$size} {
    margin: $size+px !important;
  }

  .mt-#{$size} {
    margin-top: $size+px !important;
  }

  .mr-#{$size} {
    margin-right: $size+px !important;
  }

  .mb-#{$size} {
    margin-bottom: $size+px !important;
  }

  .ml-#{$size} {
    margin-left: $size+px !important;
  }

  // PADDINGS
  .p-#{$size} {
    padding: $size+px !important;
  }

  .pt-#{$size} {
    padding-top: $size+px !important;
  }

  .pr-#{$size} {
    padding-right: $size+px !important;
  }

  .pb-#{$size} {
    padding-bottom: $size+px !important;
  }

  .pl-#{$size} {
    padding-left: $size+px !important;
  }
}

// ALIGNMENT
//////////////////////////////////////////////////////////////////////////
$values: left, center, right;

@each $value in $values {
  .align-#{$value} {
    text-align: $value !important;
  }
}


// VISIBILITY
//////////////////////////////////////////////////////////////////////////
$viewports: ("xs": "479px", "sm": "575px", "md": "767px", "lg": "991px", "xl": "1199px", "xxl": "1599px");
@each $viewport, $pixel in $viewports {
  .hidden-#{$viewport}-down {
    @media (max-width: $pixel) {
      display: none !important;
    }
  }
}

$viewports: ("xs": "480px", "sm": "576px", "md": "768px", "lg": "992px", "xl": "1200px", "xxl": "1600px");
@each $viewport, $pixel in $viewports {
  .hidden-#{$viewport}-up {
    @media (min-width: $pixel) {
      display: none !important;
    }
  }
}

// GRID CELL-VALUE
//////////////////////////////////////////////////////////////////////////
.grid--cell-value {
  &,
  .ant-row,
  .ant-col {
    .ant-card {
      .ant-card-body {
        padding: 16px;
      }
    }

    h2.ant-typography {
      @include title-16-primary;
      margin-bottom: 24px;
    }

    h3.ant-typography {
      @include title-12-primary;
    }

    h4.ant-typography {
      @include title-10;
    }

    h2.ant-typographyو
    h3.ant-typographyو
    h3.ant-typography {
      &:before {
        display: none;
      }
    }
  }
}

