.code-box-demo {
  text-align: center;
}

.ant-layout {
  background-color: transparent;
}

.app-logo {
  padding-left: 24px;
}

.ant-layout-header {
  background-color: $primary-color;
  height: auto;
  padding: 0;

  @include breakpoint(size-lg-up) {
    margin: 0 24px;
    border-radius: 10px;
  }

  .app-main-menu {
    background-color: transparent;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    border: 0;
    transition: none;
    height: 64px;
    line-height: 0;
    padding: 0 24px;

    &::-webkit-scrollbar {
      display: none;
    }

    @include breakpoint(size-lg-up) {
      justify-content: space-between;
      display: flex;
    }

    .ant-menu-item {
      @include title-16-primary;
      background-color: $primary-color;
      display: inline-block !important;
      color: $white-color;
      font-weight: normal;
      margin-top: 10px;
      margin-bottom: 0;
      padding: 15px 30px 20px;
      transition: none;
      top: auto;
      bottom: 0;
      border: 0;
      height: auto;
      overflow: visible;
      user-select: none;

      &:before,
      &:after {
        content: "";
        display: block;
        position: absolute;
        width: 40px;
        height: 40px;
        top: auto;
        bottom: -20px;
        z-index: 10;
        opacity: 1;
        border: 20px solid transparent;
        transform: scale(1);
      }

      &:before {
        left: -20px;
        right: auto;
        border-radius: 0 0 40px 0;
        border-top: 0;
        border-left: 0;
      }

      &:after {
        right: -20px;
        left: auto;
        border-radius: 0 0 0 40px;
        border-top: 0;
        border-right: 0;
      }

      a {
        color: darken($white-color, 20);
      }

      &:hover a {
        color: $white-color;
      }

      &.ant-menu-item-selected {
        border-radius: 20px 20px 0 0;
        background-color: $bg-gray;

        &:before {
          border-color: $bg-gray;
        }
  
        &:after {
          border-color: $bg-gray;
        }

        a {
          color: $primary-color;
          font-weight: 500;
        }
      }
    }
  }
}

.ant-layout-sider {
  background: transparent;

  @include breakpoint(size-lg-down) {
    display: none;
  }

  .sticky {
    padding: 24px 0;
  }
}

.app-layout {
  @include breakpoint(size-lg-up) {
    padding-top: 24px;
  }

  @include breakpoint(size-xl-up) {
    max-width: 1280px;
    margin: 0 auto;
  }

  .app-content {
    padding: 0;

    .app-inner-content {
      padding: 16px;

      @include breakpoint(size-lg-up) {
        padding: 0 24px;
      }
    }
  }
}
