@import "scss/_variables";
@import "scss/_mixins";

.bill-filter {
  border: 1px solid rgba($primary-color, 0.2);
  margin: 0 0 40px;
  position: relative;

  @include breakpoint(size-lg-down) {
    border-radius: 0;
    margin: 0 -20px 24px;
  }

  :global {
    .ant-card-body {
      padding: 16px 24px;

      .ant-row.ant-form-item {
        margin-bottom: 0;
      }

      .ant-collapse {
        background-color: transparent;
        border-radius: 0;
  
        .ant-collapse-item {
          border: 0;
          border-radius: 0;
  
          .ant-collapse-header {
            @include title-14-primary;
            position: absolute;
            top: 40px;
            right: 24px;
            border-radius: 15px !important;
            padding: 7px 8px 6px 32px;
            display: inline-block;
            background-color: #f4f7fc;

            @include breakpoint(size-lg-up) {
              top: 13px;
            }
          }
  
          .ant-collapse-content {
            border-radius: 0;
  
            .ant-collapse-content-box {
              padding: 24px 0 8px;
  
              .ant-picker {
                width: 100%;
              }
  
              .ant-btn.filter-btn {
                height: 38px;
              }
            }
          }
        }
      }
  
      .ant-radio-group {
        display: flex;
        justify-content: space-between;
  
        .ant-radio-button-wrapper {
          @include title-16-primary;
          padding: 0 15px;
          border: 0;
          border-radius: 0;
          box-shadow: 0 0 0;
          font-weight: normal;
          border-color: $coral-color !important;
  
          &:before {
            display: none;
          }
  
          &.ant-radio-button-wrapper-checked {
            border-bottom: 2px solid $coral-color;
            font-weight: 500;
          }
        }
      }
    }
  }
}
